import request from '@/utils/request'
export const sendmsgs = (text) => {
  return request.post('/sendMsg', {
    text
  })
}
export const getmsgs = () => {
  return request.post('/getMsg')
}
export const updateports = (portname, baudrate, databit, stopbits) => {
  return request.post('/updateport', {
    portname: portname,
    baudrate: baudrate,
    databits: databit,
    stopbits: stopbits
  })
}
