import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    receivedData: ''
  },
  mutations: {
    setReceivedData (state, data) {
      state.receivedData = data
    }
  },
  actions: {
    receiveData ({ commit }, data) {
      commit('setReceivedData', data)
    }
  }
})
