<script>
import { sendmsgs, getmsgs, updateports } from '@/api/sendmsg.js'
export default {
  name: 'MsgForDataPage',
  data () {
    return {
      preportname: '',
      textarea: '',
      timeoutId: null,
      textarea1: '',
      textarea2: '',
      textarea3: '',
      loading: false,
      value1: '1200',
      value2: '20',
      value3: '',
      value6: '0',
      activeName: 'first',
      label: 'COM2',
      options: [{
        value: 'COM1',
        label: 'COM1'
      }, {
        value: 'COM2',
        label: 'COM2'
      }, {
        value: 'COM3',
        label: 'COM3'
      }, {
        value: 'COM4',
        label: 'COM4'
      }, {
        value: 'COM5',
        label: 'COM5'
      }],
      reverse: true,
      activities: []
    }
  },
  computed: {
    value4 () {
      return Math.min(12000, this.value1 * 2 ** (this.value2 / 10))
    },
    value5 () {
      return 1 + (this.value3 / 10) * 0.5
    },
    value7 () {
      return 5 + (this.value6 / 10)
    },
    tyt () {
      return '‰∏≤Âè£Âè∑‰∏∫:' + this.label
    },
    receivedData () {
      return this.$store.state.receivedData
    }
  },

  methods: {
    async Getmsg () {
      const { data: { data } } = await getmsgs()
      if (data !== null && typeof data !== 'undefined' && data !== '') {
        this.loading = true
        if (this.textarea1 !== '') { this.textarea1 += '\n' + 'receive msg : ' + data; this.textarea2 += '\n' + this.StringToHex(data) } else { this.textarea1 += 'receive msg : ' + data; this.textarea2 += this.StringToHex(data) }
        setTimeout(() => {
          this.update('receive msg')
          this.loading = false
        }, 500)
      }
      if (this.intervalId) {
        clearInterval(this.intervalId)
      }
      if (this.timeoutId) {
        clearTimeout(this.timeoutId)
        this.timeoutId = null // Â¶ÇÊûú‰Ω†ÊÉ≥Âú®‰πãÂêéÈáçÊñ∞ÂºÄÂßãÂÆöÊó∂Âô®Ôºå‰Ω†ÂèØ‰ª•Â∞ÜÊ≠§Ë°åÂà†Èô§
      }
      // ËÆæÁΩÆ‰∏ã‰∏ÄÊ¨°ÊâßË°åÁöÑÂª∂ËøüÊó∂Èó¥
      this.timeoutId = setTimeout(() => {
        this.Getmsg()
      }, 10) // 0.01ÁßíÈíüÂêéÊâßË°å Getmsg ÊñπÊ≥ï
    },
    pollServer () {
      this.Getmsg()
    },

    async Sendmsg () {
      this.loading = true
      console.log(this.textarea3)
      if (this.textarea3 !== '') {
        const { data: { data } } = await sendmsgs(this.textarea3)
        setTimeout(() => {
          this.update(data)
          this.loading = false
        }, 500)
        if (this.textarea1 !== '') { this.textarea1 += '\n' + data + ': ' + this.textarea3; this.textarea2 += '\n' + this.StringToHex(this.textarea3) } else { this.textarea1 += data + ': ' + this.textarea3; this.textarea2 += this.StringToHex(this.textarea3) }
      } else {
        this.loading = false
        if (this.textarea1 !== '') { this.textarea1 += '\n:)ËØ∑ËæìÂÖ•Êï∞ÊçÆÂÜçÂèëÈÄÅ' } else { this.textarea1 += ':)ËØ∑ËæìÂÖ•Êï∞ÊçÆÂÜçÂèëÈÄÅ' }
      }
    },
    changhtml () {
      this.updatePort()
    },
    async update (data) {
      if (this.activities.length === 3) { this.activities.pop() }
      const dateW = new Date()
      this.activities.unshift({
        content: data,
        timestamp: dateW
      })
    },
    StringToHex (str) {
      let hex = ''
      for (let i = 0; i < str.length; i++) {
        if (i !== 0) { hex += '--' + str.charCodeAt(i).toString(16) } else hex += '' + str.charCodeAt(i).toString(16)
      }
      return hex
    },
    async updatePort () {
      this.loading = true
      const { data: { data, msg } } = await updateports(this.label, this.value4, this.value7, this.value5)

      if (data !== 'change success') {
        alert(msg)
        this.value2 = 30
        this.value6 = 30
        this.value3 = 0
        console.log(this.preportname)

        this.label = this.preportname
        setTimeout(() => {
          this.update(msg)
          this.loading = false
        }, 500)
      } else {
        alert(data)
        this.preportname = this.label
        setTimeout(() => {
          this.update(data)
          this.loading = false
        }, 500)
      }
    }
  },
  created () {
    this.value2 = 30
    this.value6 = 30
  },
  mounted () {
    this.preportname = this.label
    this.pollServer() // Âú®ÁªÑ‰ª∂ÊåÇËΩΩÂêéÂêØÂä®ÂÆöÊó∂Âô®
  },
  beforeDestroy () {
    clearInterval(this.intervalId) // Âú®ÁªÑ‰ª∂ÈîÄÊØÅÂâçÊ∏ÖÈô§ÂÆöÊó∂Âô®Ôºå‰ª•Èò≤Ê≠¢ÂÜÖÂ≠òÊ≥ÑÊºè
  }
}
</script>

